<template>
  <div>
    <h3>  ?</h3>
    <h4>
           . <br />
      <span> </span> .
    </h4>
    <form ref="form" @submit.prevent="sendEmail">
      <input
        ref="name"
        v-model="name"
        type="text"
        name="name"
        placeholder=" ."
        class="text"
      />
      <div class="modal-box">
        <section class="selectbox">
          <button
            type="button"
            class="selectbox-toggle-btn"
            @click="togglePosition"
          >
            {{ selectedPosition }}
          </button>
          <ul class="selectbox-option" :class="{ hide: hidePositionOptions }">
            <li v-for="pos in position" :key="pos.text">
              <button
                type="button"
                :class="{
                  'selectbox-option-btn-selected':
                    pos.text === selectedPosition,
                }"
                class="selectbox-option-btn"
                @click="selectPosition(pos.text)"
              >
                {{ pos.text }}
              </button>
            </li>
          </ul>
        </section>
        <p> ?</p>
        <div class="modal-box-radio">
          <input type="radio" id="yes" value="yes" v-model="selectedOption" />
          <label for="yes"></label>

          <input type="radio" id="no" value="no" v-model="selectedOption" />
          <label for="no"></label>
        </div>
      </div>
      <input
        ref="tools"
        v-model="tools"
        name="tools"
        type="text"
        placeholder="  ,  ."
        class="text"
      />
      <input
        ref="email"
        v-model="email"
        name="email"
        type="text"
        placeholder=" ."
        class="text"
      />
      <textarea
        ref="introduction"
        v-model="introduction"
        name="introduction"
        class="text textarea"
        cols="50"
        rows="10"
        placeholder=" ,    ."
      ></textarea>
      <input
        type="submit"
        value=""
        class="send"
        :disabled="!formIsValid"
        :class="{ 'disabled-button': !formIsValid }"
      />
    </form>
  </div>
</template>

<script>
import emailjs from "@emailjs/browser";
export default {
  data() {
    return {
      // positionOption: "",
      hidePositionOptions: true,
      position: [
        { text: "", value: "0" },
        { text: "", value: "1" },
        { text: "", value: "2" },
        { text: "()", value: "3" },
        { text: "()", value: "4" },
        { text: "(2d/3d)", value: "5" },
        { text: "", value: "6" },
      ],
      selectedOption: "no",
      selectedPosition: "",
      name: "",
      tools: "",
      email: "",
      introduction: "",
    };
  },
  computed: {
    formIsValid() {
      return (
        this.name &&
        this.selectedPosition !== "" &&
        this.tools &&
        this.email &&
        this.introduction
      );
    },
  },
  methods: {
    resetForm() {
      this.name = "";
      this.selectedPosition = "";
      this.selectedOption = "no";
      this.tools = "";
      this.email = "";
      this.introduction = "";
    },
    togglePosition() {
      this.hidePositionOptions = !this.hidePositionOptions;
    },
    selectPosition(text) {
      this.selectedPosition = text;
      this.hidePositionOptions = true;
    },
    sendEmail() {
      // emailjs   
      const serviceID = "service_5pf9han";
      const templateID = "template_dkf8qrg";
      const userID = "PZ319j5mhUdqrnBeH";

      //   
      const formData = {
        name: this.name,
        position: this.selectedPosition,
        experience: this.selectedOption,
        tools: this.tools,
        email: this.email,
        introduction: this.introduction,
      };
      //   
      if (this.submitting) {
        return; //     
      }

      //    
      this.submitting = true;
      // emailjs   
      emailjs
        .send(serviceID, templateID, formData, userID)
        .then(() => {
          //     
          console.log("  .");
          alert("  .");
          //       .
          this.resetForm();
          this.$emit("closeDialog");
        })
        .catch((error) => {
          //     
          console.error("    :", error);
          alert("    :", error);
          //        .
        })
        .finally(() => {
          //        
          this.submitting = false;
        });
    },
  },
};
</script>
<style></style>
